.navigation {
  position: fixed;
  left: 10px;
  top: 50%;
  z-index: 2;
}
.navigation .postBtn {
  height: 40px;
  width: 40px;
  display: block;
  background: #262626;
  position: absolute;
  z-index: 1;
  border-radius: 6px;
  cursor: pointer;
  -moz-transition: all 0.35s ease-in;
  -o-transition: all 0.35s ease-in;
  -webkit-transition: all 0.35s ease-in;
  transition: all 0.35s ease-in;
}
.navigation .postBtn:after {
  content: '+';
  width: 100%;
  position: absolute;
  text-align: left;
  left: 12px;
  line-height: 40px;
  font-family: 'Roboto', 'Helvetica Neue', Helvetica, sans-serif;
  color: #eae3c9;
  font-weight: 100;
  font-size: 1.8em;
  -moz-transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.navigation .postToggle {
  display: none;
}
.navigation ul {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
}
.navigation li {
  position: absolute;
  overflow: hidden;
  text-indent: -9999px;
  height: 50px;
  width: 50px;
  background: #d6d6d6;
  border-radius: 50%;
  opacity: 0;
  border: 1px solid white;
  -moz-background-clip: padding;
  -o-background-clip: padding-box;
  -webkit-background-clip: padding;
  background-clip: padding-box;
  -moz-transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -moz-transition: -moz-transform 0.3s cubic-bezier(0.48, 0.13, 0.42, 1.45), opacity 0.2s ease-in;
  -o-transition: -o-transform 0.3s cubic-bezier(0.48, 0.13, 0.42, 1.45), opacity 0.2s ease-in;
  -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.48, 0.13, 0.42, 1.45), opacity 0.2s ease-in;
  transition: transform 0.3s cubic-bezier(0.48, 0.13, 0.42, 1.45), opacity 0.2s ease-in;
}
.navigation li:nth-child(2) {
  transition-delay: 0.025s;
}
.navigation li:nth-child(3) {
  transition-delay: 0.05s;
}
.navigation li:nth-child(4) {
  transition-delay: 0.075s;
}
.navigation li:nth-child(5) {
  transition-delay: 0.1s;
}
.navigation li a {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}
.navigation .linkedin {
  background: #9f05ff69;
}
.navigation .github {
  background: #fd5e086b;
}
.navigation .email {
  background: #68748067;
}
.navigation .chat {
  background: #a26fbc;
}
.navigation .video {
  background: #363636;
}

.postToggle:checked ~ .postBtn {
  background: #0f0f0f;
  -moz-transform: scale(0.85, 0.85);
  -ms-transform: scale(0.85, 0.85);
  -webkit-transform: scale(0.85, 0.85);
  transform: scale(0.85, 0.85);
}
.postToggle:checked ~ .postBtn:after {
  content: '-';
  line-height: 38px;
  left: 16px;
}
.postToggle:checked ~ ul li {
  opacity: 1;
}
.postToggle:checked ~ ul .linkedin {
  -moz-transform: translate(0, -175%);
  -ms-transform: translate(0, -175%);
  -webkit-transform: translate(0, -175%);
  transform: translate(0, -175%);
}
.postToggle:checked ~ ul .linkedin:hover {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.1);
}
.postToggle:checked ~ ul .github {
  -moz-transform: translate(125%, -130%);
  -ms-transform: translate(125%, -130%);
  -webkit-transform: translate(125%, -130%);
  transform: translate(125%, -130%);
}
.postToggle:checked ~ ul .github:hover {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.1);
}
.postToggle:checked ~ ul .email {
  -moz-transform: translate(175%, 0%);
  -ms-transform: translate(175%, 0%);
  -webkit-transform: translate(175%, 0%);
  transform: translate(175%, 0%);
}
.postToggle:checked ~ ul .link:hover {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.1);
}
.postToggle:checked ~ ul .chat {
  -moz-transform: translate(125%, 130%);
  -ms-transform: translate(125%, 130%);
  -webkit-transform: translate(125%, 130%);
  transform: translate(125%, 130%);
}
.postToggle:checked ~ ul .chat:hover {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.1);
}
.postToggle:checked ~ ul .video {
  -moz-transform: translate(0px, 175%);
  -ms-transform: translate(0px, 175%);
  -webkit-transform: translate(0px, 175%);
  transform: translate(0px, 175%);
}
.postToggle:checked ~ ul .video:hover {
  box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.1);
}